/* Login.css */
body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  background: linear-gradient(120deg, #8bc34a, #4caf50); /* Farming Green Theme */
  font-family: "Poppins", sans-serif;
}

/* 3D Moving Background */
.animated-bg {
  position: fixed;
  width: 100vw;
  height: 100vh;
  background: url("../assets/loginbg.jpg") no-repeat center center;
  background-size: cover;
  top: 0;
  left: 0;
  opacity: 0.3;
  pointer-events: none; 
  animation: moveBackground 6s infinite alternate ease-in-out;
}

@keyframes moveBackground {
  0% { transform: translateY(0) scale(1.1); }
  100% { transform: translateY(-15px) scale(1.15); }
}

/* Login Box */
.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  flex-direction: column;
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.login-box {
  background: rgba(255, 255, 255, 0.9);
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.2);
  text-align: center;
  width: 90%;
  max-width: 400px;
  transition: transform 0.3s ease-in-out;
  z-index: 2;
}

.login-box:hover {
  transform: scale(1.05);
}

h2 {
  color: #2e7d32;
  font-size: 2rem;
}

input {
  width: 100%;
  padding: 12px;
  margin: 10px 0;
  border: 2px solid #4caf50;
  border-radius: 8px;
  outline: none;
  font-size: 16px;
  transition: border 0.3s;
}

input:focus {
  border: 2px solid #2e7d32;
}

button {
  width: 100%;
  padding: 12px;
  background: #2e7d32;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 18px;
  cursor: pointer;
  transition: background 0.3s;
}

button:hover {
  background: #1b5e20;
}

/* Responsive */
@media (max-width: 768px) {
  .login-box {
      width: 95%;
      padding: 1.5rem;
  }

  h2 {
      font-size: 1.8rem;
  }
}
