/* General Reset */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Arial", sans-serif;
}

/* Video Background */
.video-bg {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
}

/* Community Container */
.community-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 100vh;
}

/* Remove Background from Body */
body {
    color: white;
}

/* Main Dashboard Container */
.dashboard-container {
    position: relative;
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

/* Header - Modern Navbar */
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 5%;
    background: rgba(45, 106, 79, 0.9);
    color: white;
    position: sticky;
    top: 0;
    z-index: 1000;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    backdrop-filter: blur(8px); /* Glassmorphism effect */
    width: 100%;
}

/* Navbar Links */
.nav-links {
    list-style: none;
    display: flex;
    align-items: center;
    gap: 20px;
}

.nav-links li {
    display: inline;
}

.nav-links a,
.auth-button {
    text-decoration: none;
    color: white;
    font-size: 17px;
    font-weight: bold;
    padding: 10px 18px;
    border-radius: 8px;
    transition: background 0.3s ease-in-out, transform 0.2s;
    background: rgba(255, 255, 255, 0.2);
}

.nav-links a:hover,
.auth-button:hover {
    background: #a8df8e;
    color: #2d6a4f;
    transform: scale(1.05);
}

/* Dashboard Title */
.dashboard-title {
    font-size: 32px;
    font-weight: bold;
    color: black;
    text-shadow: 3px 3px 10px rgba(0, 0, 0, 0.5);
    margin-bottom: 20px;
    text-align: center;
}

/* Sensor Data Container */
.sensor-container {
    display: flex;
    gap: 20px;
    justify-content: center;
    flex-wrap: wrap;
    margin-bottom: 30px;
}

.sensor-box {
    background: rgb(60, 60, 60);
    backdrop-filter: blur(10px);
    padding: 20px;
    border-radius: 12px;
    width: 250px;
    text-align: center;
    font-size: 18px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    transition: transform 0.3s ease-in-out;
}

.sensor-box:hover {
    transform: scale(1.05);
}

.sensor-box h3 {
    font-size: 22px;
    margin-bottom: 10px;
    color: #a8df8e;
}

.sensor-box p {
    font-size: 20px;
    font-weight: bold;
}

/* Motor Control Button */
.motor-button {
    padding: 12px 25px;
    font-size: 18px;
    font-weight: bold;
    color: white;
    background: linear-gradient(10deg, #ff9800, #ff5722);
    border: none;
    border-radius: 20px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    margin-top: 20px;
}

.motor-button:hover {
    background: linear-gradient(120deg, #ff5722, #ff9800);
    transform: scale(1.05);
}

/* Chatbot Section */
.chatbot-container {
    background: rgba(255, 255, 255, 0.15);
    backdrop-filter: blur(10px);
    padding: 25px;
    border-radius: 12px;
    width: 60%;
    text-align: center;
    font-size: 18px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    margin-top: 40px;
}

.chatbot-container h3 {
    font-size: 24px;
    color: #ffaa2b;
    margin-bottom: 15px;
}

.chatbot-container p {
    font-size: 20px;
    font-weight: bold;
}

/* Footer */
footer {
    text-align: center;
    padding: 20px;
    background: rgba(45, 45, 45, 0.8);
    color: white;
    font-size: 16px;
    width: 100%;
    margin-top: 40px;
}

/* âœ… Floating Chatbot Icon */
.chatbot-icon {
    position: fixed;
    bottom: 80px;  /* Adjust above footer */
    right: 20px;   /* Align to the right */
    width: 60px;
    height: 60px;
    background-color: #4CAF50; /* Green background */
    color: white;
    font-size: 30px;
    text-align: center;
    line-height: 60px;
    border-radius: 50%;
    cursor: pointer;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    transition: transform 0.2s, background 0.3s;
}

.chatbot-icon:hover {
    background-color: #3e8e41; /* Darker green */
    transform: scale(1.1);
}

/* Responsive Design */
@media (max-width: 1024px) {
    .dashboard-title {
        font-size: 28px;
    }

    .chatbot-container {
        width: 80%;
    }
}

@media (max-width: 768px) {
    .sensor-container {
        flex-direction: column;
        align-items: center;
    }

    .sensor-box {
        width: 80%;
    }

    .chatbot-container {
        width: 90%;
    }

    .chatbot-icon {
        width: 50px;
        height: 50px;
        font-size: 25px;
        line-height: 50px;
        bottom: 60px;  /* Adjust for smaller screens */
    }
}

@media (max-width: 480px) {
    .dashboard-title {
        font-size: 24px;
    }

    .chatbot-container {
        width: 95%;
    }

    .motor-button {
        font-size: 16px;
        padding: 10px 20px;
    }

    .chatbot-icon {
        bottom: 50px; /* Adjust for very small screens */
        right: 15px;
    }
}
