/* Signup.css */
body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  font-family: "Poppins", sans-serif;
}

/* 3D Moving Background */
.signup-3d-bg {
  position: fixed;
  width: 100vw;
  height: 100vh;
  background: url("../assets/signupbg.jpg") no-repeat center center;
  background-size: cover;
  top: 0;
  left: 0;
  opacity: 0.4; /* Adjusted for better visibility */
  pointer-events: none; 
  animation: moveBackground 8s infinite alternate ease-in-out;
  z-index: -1; /* Ensures it's behind all other content */
}

@keyframes moveBackground {
  0% { transform: translateY(0) scale(1.1); }
  50% { transform: translateY(-10px) scale(1.12); }
  100% { transform: translateY(-20px) scale(1.15); }
}

/* Signup Page Styling */
.signup-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  flex-direction: column;
  position: relative;
  overflow: hidden;
  z-index: 1;
}

/* Signup Box */
.signup-box {
  background: rgba(255, 255, 255, 0.9);
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.2);
  text-align: center;
  width: 90%;
  max-width: 400px;
  transition: transform 0.3s ease-in-out;
  z-index: 2;
}

.signup-box h2 {
  color: #4CAF50;
  margin-bottom: 20px;
}

/* Input Fields */
.signup-box input {
  width: 100%;
  padding: 12px;
  margin: 10px 0;
  border: 2px solid #4caf50;
  border-radius: 8px;
  outline: none;
  font-size: 16px;
  transition: border 0.3s;
}

/* Buttons */
.signup-button, .login-button {
  background: #4CAF50;
  color: white;
  padding: 10px 15px;
  margin-top: 10px;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  font-size: 16px;
  transition: 0.3s;
}

.signup-button:hover, .login-button:hover {
  background: #388E3C;
}

/* Responsive Design */
@media (max-width: 600px) {
  .signup-box {
      width: 90%;
      padding: 20px;
  }
  .signup-box input {
      font-size: 14px;
  }
  .signup-button, .login-button {
      font-size: 14px;
      padding: 8px 12px;
  }
}
