/* General Reset */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Arial", sans-serif;
}

/* Video Background */
.video-bg {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
}

/* Community Container */
.community-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 100vh;
}

/* Header - Modern Navbar */
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 5%;
    background: rgba(45, 106, 79, 0.9);
    color: white;
    position: sticky;
    top: 0;
    z-index: 1000;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    backdrop-filter: blur(8px); /* Glassmorphism effect */
  }
  
  /* Navbar Links */
  .nav-links {
    list-style: none;
    display: flex;
    align-items: center;
    gap: 20px;
  }
  
  .nav-links li {
    display: inline;
  }
  
  .nav-links a,
  .auth-button {
    text-decoration: none;
    color: white;
    font-size: 17px;
    font-weight: bold;
    padding: 10px 18px;
    border-radius: 8px;
    transition: background 0.3s ease-in-out, transform 0.2s;
    background: rgba(255, 255, 255, 0.2);
  }
  
  .nav-links a:hover,
  .auth-button:hover {
    background: #a8df8e;
    color: #2d6a4f;
    transform: scale(1.05);
  }

/* Chat Container */
.chat-container {
    width: 80%;
    max-width: 800px;
    margin-top: 20px;
    padding: 20px;
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(10px);
    border-radius: 12px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

/* Messages Box */
.messages-box {
    max-height: 400px;
    overflow-y: auto;
    padding: 15px;
    border-radius: 8px;
    background: rgba(0, 0, 0, 0.3);
}

/* Single Message */
.message {
    margin-bottom: 15px;
    padding: 10px;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 8px;
    font-size: 16px;
}

.message a {
    color: #a8df8e;
    text-decoration: none;
}

.message a:hover {
    text-decoration: underline;
}

/* No Messages Text */
.no-messages {
    text-align: center;
    font-size: 16px;
    color: #ccc;
    font-style: italic;
}

/* Message Input */
.message-input {
    margin-top: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

textarea {
    width: 100%;
    height: 50px;
    padding: 10px;
    border-radius: 6px;
    border: none;
    resize: none;
    background: rgba(255, 255, 255, 0.2);
    color: white;
}

input[type="file"] {
    margin-top: 10px;
    color: white;
}

/* Send Button */
.send-button {
    padding: 12px 25px;
    margin-top: 10px;
    font-size: 18px;
    font-weight: bold;
    color: white;
    background: linear-gradient(120deg, #40916c, #1b4332);
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}

.send-button:hover {
    background: linear-gradient(120deg, #1b4332, #40916c);
    transform: scale(1.05);
}

/* Responsive Design */
@media (max-width: 768px) {
    .chat-container {
        width: 90%;
    }

    textarea {
        height: 40px;
    }
}

@media (max-width: 480px) {
    .chat-container {
        width: 95%;
    }

    .header {
        font-size: 18px;
    }

    .send-button {
        font-size: 16px;
        padding: 10px 20px;
    }
}
