/* General Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Arial", sans-serif;
}

/* Body Background Image */
body {
  color: white;
}

/* Main Container */
.home-container {
  background: url("../assets/homebg.jpg") no-repeat center center fixed;
  background-size: cover;
  width: 100%;
  overflow-x: hidden;
}

/* Header - Modern Navbar */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 5%;
  background: rgba(45, 106, 79, 0.9);
  color: white;
  position: sticky;
  top: 0;
  z-index: 1000;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(8px); /* Glassmorphism effect */
}

/* Navbar Links */
.nav-links {
  list-style: none;
  display: flex;
  align-items: center;
  gap: 20px;
}

.nav-links li {
  display: inline;
}

.nav-links a,
.auth-button {
  text-decoration: none;
  color: white;
  font-size: 17px;
  font-weight: bold;
  padding: 10px 18px;
  border-radius: 8px;
  transition: background 0.3s ease-in-out, transform 0.2s;
  background: rgba(255, 255, 255, 0.2);
}

.nav-links a:hover,
.auth-button:hover {
  background: #a8df8e;
  color: #2d6a4f;
  transform: scale(1.05);
}

.profile-pic {
  width: 150px; /* or 200px */
  height: 150px;
  border-radius: 50%; /* Makes it circular */
  object-fit: cover;
}

/* Hero Section */
.hero {
  position: relative;
  text-align: center;
  padding: 120px 10%;
  color: white;
  overflow: hidden;
}

/* 3D Moving Background for Hero Section */
.hero-bg {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: url("../assets/hero3d.jpg") no-repeat center center;
  background-size: cover;
  opacity: 50%;
  pointer-events: none;
  animation: zoomInOut 8s infinite alternate ease-in-out;
  z-index: -1;
}

/* Animation: Zoom In & Out */
@keyframes zoomInOut {
  0% { transform: scale(1.1); }
  50% { transform: scale(1.25); }
  100% { transform: scale(1.1); }
}

/* Hero Content */
.hero-content {
  background: rgba(255, 255, 255, 0.15);
  padding: 50px;
  border-radius: 15px;
  backdrop-filter: blur(15px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  display: inline-block;
  text-align: center;
  animation: fadeIn 1.5s ease-in-out;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

.hero h1 {
  font-size: 42px;
  font-weight: bold;
  text-shadow: 3px 3px 15px rgb(0, 0, 0);
}

.hero p {
  font-size: 22px;
  margin: 15px 0;
  color: #e0f7da;
  text-shadow: 5px 5px 15px rgb(0, 0, 0);
}

/* Call-to-Action Button */
.cta-button {
  padding: 14px 30px;
  font-size: 20px;
  font-weight: bold;
  color: white;
  background: linear-gradient(120deg, #40916c, #1b4332);
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

.cta-button:hover {
  background: linear-gradient(120deg, #1b4332, #40916c);
  transform: scale(1.05);
}

/* Sections with Blur Box */
section {
  padding: 50px 10%;
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(12px);
  border-radius: 15px;
  margin: 30px auto;
  max-width: 90%;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  text-align: center;
}

/* Section Titles */
h2 {
  font-size: 28px;
  margin-bottom: 20px;
  color: #fff;
}

/* Featured Blogs */
.blog-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
}

.blog-card {
  background: rgba(255, 255, 255, 0.2);
  padding: 20px;
  border-radius: 10px;
  text-align: center;
  font-size: 18px;
  cursor: pointer;
  transition: transform 0.3s ease, background 0.3s;
}

.blog-card:hover {
  background: rgba(168, 223, 142, 0.3);
  transform: scale(1.05);
}

/* Categories */
.categories ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  list-style: none;
  gap: 15px;
}

.categories li {
  padding: 12px 20px;
  background: rgba(82, 183, 136, 0.7);
  color: white;
  border-radius: 6px;
  font-size: 16px;
  cursor: pointer;
  transition: background 0.3s ease-in-out, transform 0.2s;
}

.categories li:hover {
  background: #1b4332;
  transform: translateY(-3px);
}

/* Footer */
footer {
  text-align: center;
  padding: 20px;
  background: rgba(45, 45, 45, 0.8);
  color: white;
  font-size: 16px;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .hero {
    padding: 100px 5%;
  }

  .hero h1 {
    font-size: 38px;
  }

  .cta-button {
    font-size: 18px;
  }
}

@media (max-width: 768px) {
  .header {
    flex-direction: column;
    text-align: center;
  }

  .nav-links {
    flex-direction: column;
    gap: 10px;
  }

  .hero h1 {
    font-size: 32px;
  }

  .cta-button {
    font-size: 16px;
    padding: 10px 20px;
  }

  section {
    padding: 40px 8%;
  }
}

@media (max-width: 480px) {
  .hero {
    padding: 80px 5%;
  }

  .hero h1 {
    font-size: 28px;
  }

  .hero p {
    font-size: 18px;
  }

  .cta-button {
    font-size: 14px;
    padding: 8px 16px;
  }

  section {
    padding: 30px 5%;
  }
}
